import animationJSON from "../../assets/images/preloader.json";

const MIN_PRELOADER_WORKING_TIME = 1000;
const PRELOADER_OPACITY_DURATION = 200; 

const preloader = document.getElementById("preloader");
const preloaderWrapper = document.querySelector(".preloader__wrapper");

history.scrollRestoration = "manual";

startPreloaderAnimation();

window.addEventListener("DOMContentLoaded", () => {
  setTimeout(start, MIN_PRELOADER_WORKING_TIME)
});

function startPreloaderAnimation() {
  bodymovin.loadAnimation({
    container: preloader,
    animationData: animationJSON,
    renderer: "svg",
    loop: true,
    autoplay: true,
    name: "Preloader Logo Animation",
  });
}

function start() {
  const inner = document.querySelector(".inner");

  preloaderWrapper.style.transitionProperty = "opacity";
  preloaderWrapper.style.transitionDuration = `${PRELOADER_OPACITY_DURATION / 1000}s`;
  preloaderWrapper.style.opacity = 0;

  setTimeout(() => {
    inner.style.opacity = 1;
  }, PRELOADER_OPACITY_DURATION);
}
